.homepage_container {
  display: flex;
  flex-direction: column;
  height: 115vh;
  margin-top: -5vw;
  .homepage_wrapper {
    height: inherit;
    .rect_design_container {
      position: absolute;
      right: 2%;

      top: 7%;

      .rect_design {
        z-index: 4;
        position: relative;
        transform: scale(1.13);
        //  transform: translate(-208px, 198px);
        &:first-of-type {
          right: 26%;
          top: 27%;
          position: absolute;
          z-index: 5;
        }
      }
    }

    .homepage_subtitle {
      position: absolute;
      width: 504px;
      height: 98px;
      left: 240px;
      top: 217px;
      // font-family: "Nunito";
      font-style: normal;
      font-weight: 400;
      font-size: 36px;
      line-height: 59px;
      color: #3e3f43;

      height: 32vw;
      // margin-top: -5vw;
      padding: 46px;
    }
    .homepage_explorebtn {
      width: 210px;
      height: 58px;
      /* left: 102px; */
      /* top: 543px; */
      background: #e89f71;
      border-radius: 8px;
      border: none;
      // font-family: 'Nunito';
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      color: white;
      cursor: pointer;

      &:hover{
        
        animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
        transform: translate3d(0, 0, 0);
        backface-visibility: hidden;
        perspective: 1000px;
      }
    }
    
  }

  .homepage_badges {
    display: flex;
    flex-direction: row;
    justify-content: center;
    bottom: 0;

    .homepage_bdg {
      display: flex;
      flex-direction: row;
      font-family: "Gilroy";
      font-style: normal;
      color: #464141;
      line-height: 48%;
      padding: 10px 70px;

      & svg {
        transform: translate(-19px, 14px);
        font-size: 35px;
        color: #e89f71;
      }

      & p {
        align-items: flex-end;
      }

      & p:first-of-type {
        font-weight: 600;
        font-size: 17px;
        color: black;
      }
    }
  }
}
